import List from '../List/list'
export default {
	components:{
	  List
	},
	data(){
		return{
			serve: ['促销', '分期', '仅看有货'], //弹出页数据
			rserve: ['耳机', '户外', '配件'],	//弹出页数据
			active: 0,		 //弹出页下标
			actives: 0,		 //弹出页下标
			show: false,	 //弹出页显示
			oneshow: true,	 //搜索记录页面
			towshow: false,	 //搜索结果页面
			changeVaule: '', // 输入框输入的值
			seekresult : []	 ,//搜索结果
			shopnames:'小米'	//搜索记录
		}
	},
	methods: {
		getBat(e) {				//弹出页文字选择点击事件1
			let index = e.currentTarget.dataset.id
			this.active = index
		},
		getBats(e) {			//弹出页文字选择点击事件2
			let indexs = e.currentTarget.dataset.id
			this.actives = indexs
		},
		getHide() {				//弹出页显示
			this.show = true
		},
		shows() {				//弹出页隐藏
			this.show = false
		},
		reset() {				//弹出重置
			this.active = 0
			this.actives = 0
		},
		searchs(){				//搜索
			this.oneshow = false
			this.towshow = true
			// uni.request({
			// 	url: 'http://ceshi3.dishait.cn/api/goods/search',
			// 	data: {
			// 		title : this.changeVaule,
			// 		page : 1
			// 	},
			// 	method:'POST',
			// 	success: (res) => {
			// 		this.seekresult=res.data.data
			// 	}
			// })
			
		},
		back() {			//返回
			uni.navigateBack({
				delta: 1
			})	
		},
		blacks(){			//遮罩层
			this.show = false
		},
		ttext(){			//搜索记录
			this.changeVaule = this.shopnames
			this.oneshow = false
			this.towshow = true
			uni.request({
				url: 'http://ceshi3.dishait.cn/api/goods/search',
				data: {
					title : this.changeVaule,
					page : 1
				},
				method:'POST',
				success: (res) => {
					this.seekresult=res.data.data
				}
			})
		}
		
	}
	
}
